<template>
    <div>
      <input type="file" @change="handleFileChange" />
      <table>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <!-- 其他字段 -->
        </tr>
        <tr>
          <td>{{ extractedData.name }}</td>
          <td>{{ extractedData.phone }}</td>
          <!-- 其他字段 -->
        </tr>
      </table>
    </div>
  </template>
  
  <script>
     import Tesseract from 'tesseract.js';
  export default {
    data() {
      return {
        extractedData: {
          name: '',
          phone: '',
          // 其他字段
        },
        apiKey: 'YOUR_GOOGLE_CLOUD_API_KEY',
      };
    },
    mounted(){
     

        Tesseract.recognize(
        'https://tesseract.projectnaptha.com/img/eng_bw.png',
        'eng',
        { logger: m => console.log(m) }
        ).then(({ data: { text } }) => {
        console.log(text);
        })
    },
    methods: {
      async handleFileChange(event) {
        const file = event.target.files[0];
        if (file) {
          const imageData = new FormData();
          imageData.append('image', file);
  
          const response = await fetch(
            `https://vision.googleapis.com/v1/images:annotate?key=${this.apiKey}`,
            {
              method: 'POST',
              body: JSON.stringify({
                requests: [
                  {
                    image: {
                      content: imageData,
                    },
                    features: [
                      {
                        type: 'TEXT_DETECTION',
                        maxResults: 1,
                      },
                    ],
                  },
                ],
              }),
            }
          );
  
          const data = await response.json();
          const extractedText = data.responses[0].fullTextAnnotation.text;
  
          // 在实际中，你需要编写代码来从提取的文本中解析出姓名、电话等信息
          // 并将信息填充到extractedData对象中
  
          this.extractedData = {
            name: 'John Doe',
            phone: '123-456-7890',
            // 其他字段
          };
        }
      },
    },
  };
  </script>
  
  <style>
  /* 样式代码 */
  </style>
  